export function transformFalkorDBToTreeData(falkorResponse) {
  // Assuming '1002' is the root distributor's distId
  const rootDistId = falkorResponse.data[0].path.nodes[0].properties.distId.toString();

  // Create a root node
  const rootNode = {
    id: rootDistId,
    name: falkorResponse.data[0].path.nodes[0].properties.name || rootDistId,
    children: [],
  };

  // A map to store nodes by distId
  const nodeMap = {
    [rootDistId]: rootNode,
  };

  // Iterate over each path
  falkorResponse.data.forEach((item) => {
    const pathNodes = item.path.nodes;
    let currentNode = rootNode;

    // Start from index 1 since index 0 is the root node
    for (let i = 1; i < pathNodes.length; i++) {
      const nodeData = pathNodes[i];
      const distId = nodeData.properties.distId.toString();
      const name = nodeData.properties.name || distId;

      // Check if the node already exists in the nodeMap
      if (!nodeMap[distId]) {
        nodeMap[distId] = {
          id: distId,
          name: name,
          children: [],
        };
      }

      const childNode = nodeMap[distId];

      // Check if the child is already added to the current node's children
      if (!currentNode.children.find((child) => child.id === distId)) {
        currentNode.children.push(childNode);
      }

      // Move to the child node
      currentNode = childNode;
    }
  });

  return rootNode;
}
