import { Graph, treeToGraphData } from "@antv/g6";
import { useEffect, useRef } from "react";
import { transformFalkorDBToTreeData } from "./transformFalkorDBToG6Data";

export function PersonalEnrollmentTree({ falkorResponse }) {
  const containerRef = useRef(null);

  useEffect(() => {
    console.log("falkorResponse: ", falkorResponse);
    const data = transformFalkorDBToTreeData(falkorResponse);
    console.log("tranformed data: ", data);
    const graph = new Graph({
      container: containerRef.current,
      data: treeToGraphData(data),
      behaviors: ["drag-canvas", "zoom-canvas", { key: "hover-activate", type: "hover-activate", degree: 5, direction: "out", inactiveState: "inactive" }],
      autoResize: true,
      animation: true,
      node: {
        style: {
          labelText: (d) => d.id,
          labelBackground: true,
        },
        animation: true,
      },
      layout: {
        type: "dendrogram",
        radial: true,
        nodeSep: 40,
        rankSep: 140,
      },
    });

    graph.render();
  }, []);

  return (
    <div>
      <div>Use G6 in react</div>
      <div ref={containerRef} />
    </div>
  );
}
